import CloseIcon from '@material-ui/icons/Close';
import styled from 'styled-components';

const ModalIcon = styled(CloseIcon)`
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 32px;
  height: 32px;
  padding: 0;
  z-index: 10;
`;

export default ModalIcon;
